* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: lightseagreen;
}

header {
  background-color: #82f382;
  padding: 20px 10px;
  font-weight: bold;
  width: 60%;
  max-width: 600px;
  margin: auto;
  text-align: center;
}

.list {
  padding: 20px 10px;
  border: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
  width: 60%;
  max-width: 600px;
  margin: auto;
}

.listname {
  width: calc(100% - 100px);
  font-weight: bold;
  font-size: 1.5rem;
}

.good>span {
  cursor: pointer;
}

.date {
  width: 100%;
  font-size: 0.75rem;
  padding-top: 10px;
}

.modal {
  background-color: #eee;
  padding: 20px;
  position: absolute;
  top: 100px;
  right: 0;
  width: 50%;
}

.modal2 {
  top: 200px;
  background-color: aliceblue;
}

button {
  border: none;
  background-color: #82f382;
  width: 100px;
  height: 50px;
}

.input {
  max-width: 600px;
  width: 60%;
  margin: auto;
  text-align: center;
  padding: 10px 0;
  border: 1px solid lightgray;
}

input {
  height: 40px;
  width: 300px;
  border: 1px solid #82f382;
}

input:focus {
  border: transparent;
}

.good,
.bad {
  width: 50px;
}

.txt {
  width: 100%;
}





a{
  position: relative;
  display: inline-block;
  padding: 25px 30px;
  margin: 40px 0;
  color: #03e9f4;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  letter-spacing: 4px;
  overflow: hidden;
  margin-right: 50px;
  filter: hue-rotate(270deg);
 
}
a:hover{
  background: #03e9f4;
  color: #050801;
  box-shadow: 0 0 5px #03e9f4,
              0 0 25px #03e9f4,
              0 0 50px #03e9f4,
              0 0 200px #03e9f4;
   -webkit-box-reflect:below 1px linear-gradient(transparent, #0005);
}
a span{
  position: absolute;
  display: block;
}
a span:nth-child(1){
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg,transparent,#03e9f4);
  animation: animate1 1s linear infinite;
}
@keyframes animate1{
  0%{
      left: -100%;
  }
  50%,100%{
      left: 100%;
  }
}
a span:nth-child(2){
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg,transparent,#03e9f4);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}
@keyframes animate2{
  0%{
      top: -100%;
  }
  50%,100%{
      top: 100%;
  }
}
a span:nth-child(3){
  bottom: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg,transparent,#03e9f4);
  animation: animate3 1s linear infinite;
  animation-delay: 0.50s;
}
@keyframes animate3{
  0%{
      right: -100%;
  }
  50%,100%{
      right: 100%;
  }
}


a span:nth-child(4){
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg,transparent,#03e9f4);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}
@keyframes animate4{
  0%{
      bottom: -100%;
  }
  50%,100%{
      bottom: 100%;
  }
}